.tu-button {
  display: inline-block;
  cursor: pointer;
  box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  padding: 10px 18px;
  font-size: 14px;
  border-radius: 4px;
  border: none; }
  .tu-button--default {
    color: #9baacf;
    background: transparent; }
    .tu-button--default:hover {
      color: #6d5dfc; }
    .tu-button--default:active {
      box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }
  .tu-button--primary {
    transition: 0.1s ease;
    background: #6d5dfc;
    box-shadow: inset 2px 2px 10px #8abdff, inset -2px -2px 10px #510ccf, 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    color: #E4EBF5; }
    .tu-button--primary:hover {
      color: #ffffff; }
    .tu-button--primary:active {
      box-shadow: inset 2px 2px 10px #510ccf, inset -2px -2px 10px #8abdff; }
  .tu-button--success {
    transition: 0.1s ease;
    background: #6bc43f;
    box-shadow: inset 2px 2px 10px #93e769, inset -2px -2px 10px #3d8618, 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    color: #E4EBF5; }
    .tu-button--success:hover {
      color: #ffffff; }
    .tu-button--success:active {
      box-shadow: inset 2px 2px 10px #3d8618, inset -2px -2px 10px #93e769; }
  .tu-button--info {
    transition: 0.1s ease;
    background: #909399;
    box-shadow: inset 2px 2px 10px #c5c5c5, inset -2px -2px 10px #54585f, 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    color: #E4EBF5; }
    .tu-button--info:hover {
      color: #ffffff; }
    .tu-button--info:active {
      box-shadow: inset 2px 2px 10px #54585f, inset -2px -2px 10px #c5c5c5; }
  .tu-button--warning {
    transition: 0.1s ease;
    background: #e6a23c;
    box-shadow: inset 2px 2px 10px #ffc979, inset -2px -2px 10px #b67617, 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    color: #E4EBF5; }
    .tu-button--warning:hover {
      color: #ffffff; }
    .tu-button--warning:active {
      box-shadow: inset 2px 2px 10px #b67617, inset -2px -2px 10px #ffc979; }
  .tu-button--danger {
    transition: 0.1s ease;
    background: #f56c6c;
    box-shadow: inset 2px 2px 10px #ff9f9f, inset -2px -2px 10px #c44444, 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    color: #E4EBF5; }
    .tu-button--danger:hover {
      color: #ffffff; }
    .tu-button--danger:active {
      box-shadow: inset 2px 2px 10px #c44444, inset -2px -2px 10px #ff9f9f; }
  .tu-button--small {
    padding: 6px 10px;
    font-size: 13px; }
  .tu-button--medium {
    padding: 10px 18px; }
  .tu-button--large {
    padding: 14px 22px; }
  .tu-button.is-round {
    border-radius: 40px; }
  .tu-button.is-circle {
    border-radius: 50%; }
